:root {
    --background_page: #f9f9f9 !important;
}
div.Icon--24 {
    text-align: center;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
}
.buttons_custom.Icon--24 {
    width: 48px !important;
    height: 64px !important;
}
.buttons_custom.Icon--24>svg {
    width: 42px !important;
    height: 44px !important;
}
div.Icon--32 {
    text-align: center;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
}
.buttons_custom.Icon--32 {
    width: 48px !important;
    height: 64px !important;
}
.buttons_custom.Icon--32>svg {
    width: 42px !important;
    height: 44px !important;
}
.Cell--ios .Cell__before-in > .Icon {
    padding: 3px 22px 10px 0;
}
.CellIconCustom {
    width: 42px !important;
    height: 44px !important;
}
.Cell--android .Cell__before-in > .Icon, .Cell--android .Cell__dragger > .Icon {
    padding: 3px 19px 4px 0;
}
.btn {
    text-decoration: none;
    border: none;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border-radius: 10px;
    font-weight: bold;
    color: #E5FFFF;
}
.btn-rating {
    text-align: center;
    background: #0052D4;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #6FB1FC, #4364F7, #0052D4);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #6FB1FC, #4364F7, #0052D4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    -webkit-box-shadow: 0px 0px 8px 0px #0052D4;
    -moz-box-shadow:    0px 0px 8px 0px #0052D4;
    box-shadow:         0px 0px 8px 0px #0052D4;
}

.btn-exchange {
    text-align: center;
    background: rgb(168,53,255);
    background: linear-gradient(176deg, rgba(168,53,255,1) 5%, rgba(160,0,191,1) 61%);
    -webkit-box-shadow: 0px 0px 8px 0px rgb(168,53,255);
    -moz-box-shadow:    0px 0px 8px 0px rgb(168,53,255);
    box-shadow:         0px 0px 8px 0px rgb(168,53,255);
}

.btn-shop {
    text-align: center;
    background: #f46b45;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #eea849, #f46b45);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #eea849, #f46b45); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    -webkit-box-shadow: 0px 0px 8px 0px #f46b45;
    -moz-box-shadow:    0px 0px 8px 0px #f46b45;
    box-shadow:         0px 0px 8px 0px #f46b45;
}

button:focus {
    outline: 0;
    color: whitel;
}

.text-buttons {
    font-size: 15px;
    font-weight: 400;
}

.HeaderButton--android .Icon--24 {
    padding: 0 !important;
}

.buttons {
    margin-top: 25px;
}

.click__item:active {
    opacity: 0.8;
}

.Search--android.Search--default .Search__before .Icon, .Search--android.Search--default .Search__after .Icon {
    padding: 0px;
    color: #aaaeb3;
    color: #aaaeb3;
    color: var(--icon_secondary);
}

